-buttonToolbar {\n\t@include btn(14px, rgba(255, 255, 255, 0), #fff, #fff, 8px 30px);\n\tborder-radius: 5px;\n\tmax-width: 200px;\n\toverflow: hidden;\n}\n\n.section-buttonFooter {\n\t@include btn(14px, rgba(255, 255, 255, 0), $main_text_color, $main_text_color, 8px 30px);\n\tborder-radius: 5px;\n\tmax-width: 200px;\n\toverflow: hidden;\n}\n\n.section_h1 {\n\tfont-size: 48px;\n\tfont-weight: 800;\n}\n.section_h2 {\n\tfont-size: 36px;\n\tfont-weight: 800;\n}\n.section_h3 {\n\tfont-size: 24px;\n\tfont-weight: 700;\n\tmargin-top: 30px;\n}\n.section_h4 {\n\tfont-size: 18px;\n}\n\n.section-button a {\n\tcolor: inherit;\n\tfont-weight: 800;\n}\n.section-button:hover {\n\tcursor: pointer;\n\tbackground: linear-gradient(232.56deg, #f78318 -13.08%, #c72400 130.61%);\n\tborder: 1px solid $forButtonColor;\n}\n.section-buttonWhite:hover {\n\tcursor: pointer;\n\tbackground: rgb(231, 231, 231);\n\tfilter: inherit;\n}\n.section-buttonToolbar:hover {\n\tcursor: pointer;\n\tbackground: #fff;\n\tcolor: $accentColor !important;\n}\n\n.section-subtitle {\n\tmargin-top: 20px;\n\tmax-width: 1040px;\n\twidth: 100%;\n}\n\n.section-offer {\n\tdisplay: grid;\n\tgrid-gap: 16px;\n}\n\n.list {\n\tmargin: 24px 0;\n\tdisplay: grid;\n\tgrid-gap: 20px;\n\tfont-size: 18px;\n\t.list__li {\n\t\tposition: relative;\n\t\tpadding-left: 30px;\n\t\tdisplay: grid;\n\t\talign-content: center;\n\t\tmin-height: 18px;\n\t}\n\t.list__li_circle {\n\t\tposition: relative;\n\t\tpadding-left: 20px;\n\t\tdisplay: grid;\n\t\talign-content: center;\n\t\tdisplay: inline;\n\t\tmargin-left: 20px;\n\t}\n\t.list__li::before {\n\t\tcontent: '';\n\t\tposition: absolute;\n\t\twidth: 18px;\n\t\tleft: 0;\n\t\theight: 18px;\n\t\tbackground: url('https://www.pulscen.ru/system/ckeditor_assets/pictures/281055/content_vector_120.png') no-repeat 100% 100% / cover;\n\t}\n\t.list__li_gray::before {\n\t\tbackground: url('https://www.pulscen.ru/system/ckeditor_assets/pictures/280239/content_group_410.png') no-repeat 100% 100% / cover;\n\t